  .collaborate_right_block_content a img {
    max-width: 145px;
  }

  .collaborate_left_block {
    width: 499px;
    height: 330px;
    position: relative;
  }

  .collaborate_left_block_content a img {
    position: absolute;
    bottom: -5%;
    left: 38%;
  }

  .collaborate_right_block {
    width: 499px;
    height: 300px;
    position: relative;
  }

  .collaborate_right_block_content a img {
    position: absolute;
    bottom: -5%;
    left: 38%;
  }

  .collaborate_left_block button {
    width: 200px;
    height: 60px;
    top: -6%;
    left: 33%;
    font-size: 22px;
  }

  .collaborate_right_block button {
    width: 200px;
    height: 60px;
    top: -6%;
    left: 33%;
    font-size: 22px;
  }

  .collaborate_content {
    gap: 70px;
    padding-bottom: 50px;
  }
}
