.header {
    background-color: rgba(255, 255, 255, 70%);
    height: 80px;
    width: 100%;
    position: fixed;
    z-index: 999;
}

.header_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

.header_container img {
    cursor: pointer;
}

.nav {
    display: flex;
    gap: 10px;
}

.nav a {
    color: #3A3333;
    text-transform: uppercase;
    font-family: 'Merriweather', serif;
    font-weight: 700;
    padding: 12px 25px;
    cursor: pointer;
    transition: all 0.5s ease;
}

.nav a:hover {
    color: #fff;
    background-color: #FED494;
    border-radius: 50px;
    transition: all 0.5s ease;
}


.nav .active {
    background-color: #FED494;
    color: #fff;
    border-radius: 50px;
}

@media screen and (max-width: 1537px) {
    .header {
        height: 55px;
    }

    .header_container {
        height: 55px;
    }

    .nav a {
        padding: 8px 15px;
        font-size: 14px;
    }

    .header_container img {
        max-width: 60px;
    }
}

@media screen and (max-width: 1025px) {
    .header {
        height: 40px;
    }

    .header_container {
        height: 40px;
        padding: 0 10px;
    }

    .nav a {
        padding: 5px 10px;
        font-size: 12px;
    }

    .header_container img {
        max-width: 40px;
    }
}