.welcome {
    width: 100vw;
    height: 100vh;
    /* background-image: url(../../img/1_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat; */

}

.welcome_wrapper {
    width: 100vw;
    height: 100vh;
    /* background-color: rgba(0, 0, 0, 20%); */
}


.welcome_content {
    padding-top: 120px;
}

.container {
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
}


.welcome_bird {
    width: 180px;
    height: 200px;
    margin-bottom: -20px;
}

.welcome_fabula {
    height: 195px;
    width: 840px;
}

.welcome_subtitle {
    font-size: 35px;
    font-weight: 700;
    margin-bottom: -20px;
    font-family: "Merriweather", sans-serif;
    text-transform: uppercase;
    color: #fff;
    padding: 0;
    text-shadow: 4px 4px #0000;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
}

.welcome_title {
    font-size: 24px;
    font-weight: 700;
    font-family: "Merriweather", sans-serif;
    color: #fff;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 50px;
}

.btn {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-bottom: 30px;
}

.btn a {
    font-size: 24px;
    font-weight: 700;
    font-family: "Merriweather", sans-serif;
    color: #3A3333;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    background-color: #fff;
    border-radius: 50px;
    padding: 15px 30px;
    transition: all 0.5s ease;
    cursor: pointer;
}

.btn a:hover {
    color: #fff;
    background-color: #FED494;
    transition: all 0.5s ease;
}

.socials {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-bottom: 40px;
    transition: all 0.5s ease;
}

.socials svg {
    fill: #fff;

}

.socials svg:hover {
    fill: #FED494;
    transition: all 0.5s ease;
    cursor: pointer;
}



@media screen and (max-width: 1537px) {
    .welcome_bird {
        width: 105px;
        height: 114px;
    }

    .welcome_subtitle {
        font-size: 28px;
    }

    .welcome_fabula {
        height: 145px;
        width: 540px;
    }

    .welcome_title {
        font-size: 16px;
        max-width: 655px;
    }

    .btn a {
        font-size: 16px;
    }

    .socials svg {
        width: 35px;
        height: 35px;
    }
}

@media screen and (max-width: 426px) {
    .welcome {
        width: 100%;
        height: 100%;
    }

    .welcome_wrapper {
        width: 100%;
        height: 100%;
    }

    .welcome_content {
        padding-top: 70px;
    }

    .welcome_subtitle {
        font-size: 22px;

    }

    .welcome_title {
        font-size: 14px;
    }

    .welcome_fabula {
        height: 90px;
        width: 365px;
    }

    .btn a {
        padding: 10px 20px;
    }
}