.gameDesignDeck {
  width: 100vw;
  height: 100vh;
  /* background-image: url(../../img/3_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat; */
}

.gameDesignDeck_wrapper {
  width: 100vw;
  height: 100vh;
  /* background-color: rgba(0, 0, 0, 20%); */
}

.container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  height: 100%;
}

.gameDesignDeck_content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 85px;
  padding-top: 100px;
}

.gameDesignDeck_content a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 218.6px;
  height: 61.7px;
  background: #fed494;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 50px;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: all 0.3s linear;
}

.gameDesignDeck_content a:hover {
  background: #ffffff;
  color: #4f4f4f;
}

.left_block {
  padding: 20px 40px;
  background: rgba(217, 217, 217, 0.44);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 70px;
  position: relative;
}

.left_block button {
  position: absolute;
  width: 300px;
  height: 70px;
  top: -6%;
  left: 23%;
  background: #fed494;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
  font-family: 'Luckiest Guy';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 32px;
  color: #583333;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
}

.left_block_content h2 {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.left_block_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.left_block_content ul li {
  text-align: start;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.left_block_content a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 50px;
  color: #4f4f4f;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 284px;
  height: 63px;
  background: #ffffff;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  text-decoration: none;
  margin-top: 15px;
  transition: all 0.3s linear;
}

.left_block_content a:hover {
  background: #fed494;
  color: #ffffff;
}

.right_block {
  padding: 20px 40px;
  background: rgba(217, 217, 217, 0.44);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 70px;
  position: relative;
}

.right_block button {
  position: absolute;
  width: 300px;
  height: 70px;
  top: -6%;
  left: 23%;
  background: #fed494;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
  font-family: 'Luckiest Guy';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 32px;
  color: #583333;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: none;
}

.right_block_content h2 {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.right_block_content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.right_block_content ul li {
  text-align: start;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.right_block_content a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 50px;
  color: #4f4f4f;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 284px;
  height: 63px;
  background: #ffffff;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  text-decoration: none;
  margin-top: 15px;
  transition: all 0.3s linear;
}

.right_block_content a:hover {
  background: #fed494;
  color: #ffffff;
}

.right_block img {
  width: 464px;
  height: 281px;
}

.left_block img {
  width: 464px;
  height: 281px;
}

@media screen and (max-width: 1537px) {
  .gameDesignDeck_content {
    margin-top: 95px;
  }

  .left_block button {
    position: absolute;
    max-width: 250px;
    max-height: 50px;
    top: -4%;
    left: 28%;
    font-size: 24px;
  }

  .right_block button {
    position: absolute;
    max-width: 250px;
    max-height: 50px;
    top: -4%;
    left: 28%;
    font-size: 24px;
  }

  .left_block_content h2 {
    font-size: 18px;
  }

  .right_block_content h2 {
    font-size: 18px;
  }

  ul {
    margin-top: 0;
  }

  .left_block_content ul li {
    font-size: 16px;
  }

  .right_block_content ul li {
    font-size: 16px;
  }

  .left_block img {
    width: 327px;
    height: 195px;
  }

  .right_block img {
    width: 327px;
    height: 195px;
  }

  .left_block {
    width: 441.469px;
    height: 463.875px;
  }

  .right_block {
    width: 441.469px;
    height: 463.875px;
  }

  .left_block_content a {
    font-size: 16px;
    width: 204px;
    height: 53px;
  }

  .right_block_content a {
    font-size: 16px;
    width: 204px;
    height: 53px;
  }

  .gameDesignDeck_content {
    gap: 0;
    column-gap: 50px;
  }

  .linkGameDeck {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .gameDesignDeck {
    height: 100%;
  }

  .gameDesignDeck_wrapper {
    height: 100%;
  }
}

