.footer {
  width: 100vw;
  height: 100%;
  /* background-image: url(../../img/3_bg.jpg);
     background-size: cover;
    background-repeat: no-repeat; */
}

.footer_wrapper {
  width: 100vw;
  height: 100%;
  /* background-color: rgba(0, 0, 0, 20%); */
}

.container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  height: 100%;
}

.footer_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  gap: 25px;
}

.icons_footer {
  display: flex;
  gap: 60px;
  margin-top: 120px;
}

.icons_footer img {
  cursor: pointer;
  background: none;
  border-radius: 50%;
  transition: all 0.3s linear;
  width: 65px;
}

.icons_footer img:hover {
  background: #fed494;
}

.links_footer {
  display: flex;
  gap: 50px;
}

.links_footer a {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 40px;
  color: #ffffff;
  text-decoration: none;
  transition: all 0.3s linear;
  cursor: pointer;
}

.links_footer a:hover {
  color: #fed494;
}

.back_footer h6 {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 40px;
  color: #ffffff;
}

@media screen and (max-width: 1537px) {
  .icons_footer {
    display: flex;
    gap: 30px;
    margin-top: 50px;
  }

  .icons_footer img {
    width: 40px;
  }

  .links_footer a {
    font-size: 14px;
  }

  .back_footer h6 {
    font-size: 14px;
    margin: 0;
  }
}
@media screen and (max-width: 426px) {
  .icons_footer {
    margin-top: 30px;
  }
  .links_footer {
    gap: 10px;
  }
  .links_footer a {
    font-size: 10px;
    line-height: 120%;
  }
  .back_footer h6 {
    font-size: 10px;
  }
}
