.heroes {
  width: 100vw;
  height: 100vh;
  /* background-image: url(../../img/3_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat; */
}

.heroes_wrapper {
  width: 100vw;
  height: 100vh;
  /* background-color: rgba(0, 0, 0, 20%); */
}

.container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  height: 100%;
}

.heroes_content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.heroes_left_block {
  display: flex;
  justify-content: space-between;
  width: 45%;
}

.heroes_right_block {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  width: 55%;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.images img {
  max-height: 700px;
}

.heroes_title h2 {
  font-family: 'Luckiest Guy';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 40px;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-top: 200px;
}

.heroes_title h5 {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 640px;
}

.heroes_content_title {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.heroes_description {
  background: rgba(0, 0, 0, 0.46);
  border-radius: 20px;
  padding: 30px;
}

.heroes_description div {
  display: flex;
  align-items: center;
  gap: 5px;
}

.heroes_description div span {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 15px;
  color: #29b1ec;
}

.heroes_description div h2 {
  font-family: 'Merriweather';
  font-style: normal;
  font-size: 18px;
  line-height: 15px;
  color: #ffffff;
}

.heroes_skills {
  margin-bottom: 120px;
  display: flex;
  justify-content: flex-end;
}

.heroes_skills img {
  width: 35%;
}

@media screen and (max-width: 1537px) {
  .heroes_content_title {
    display: flex;
    flex-direction: row;
    gap: 20px;
    flex-wrap: wrap;
  }

  .images img {
    max-height: 680px;
  }

  .heroes_skills img {
    width: 65%;
    height: 210px;
  }

  .heroes_skills {
    width: 30%;
    margin-bottom: 0px;
  }

  .heroes_description {
    padding: 15px;
    width: 55%;
  }

  .heroes_title h2 {
    font-size: 34px;
    margin-top: 0px;
  }

  .heroes_description div span {
    font-size: 16px;
  }

  .heroes_description div h2 {
    font-size: 14px;
  }
}

@media screen and (max-width: 1025px) {
  .container {
    padding: 0 10px;
  }

  .images img {
    max-height: 480px;
  }

  .heroes_title h5 {
    font-size: 14px;
    margin: 0;
  }

  .heroes_title h2 {
    margin: 0;
  }

  .heroes_description div span {
    font-size: 14px;
  }

  .heroes_description div h2 {
    font-size: 12px;
  }
}
@media screen and (max-width: 426px) {
  .heroes {
    width: 100%;
    height: 100%;
  }
  .heroes_wrapper {
    width: 100%;
    height: 100%;
  }
  .images img {
    max-height: 200px;
  }
  .heroes_right_block {
    position: relative;
  }
  .heroes_title {
    position: absolute;
    top: -140%;
    left: -10%;
  }
  .heroes_title h2 {
    font-size: 16px;
  }
  .heroes_title h5 {
    font-size: 12px;
    max-width: 200px;
  }
  .heroes_description {
    position: absolute;
    bottom: -680%;
    left: -80%;
    width: 95%;
  }
  .heroes_skills {
    position: absolute;
    bottom: -790%;
    left: 70%;
  }
  .heroes_skills img {
    width: 220%;
    height: 260px;
  }
  .heroes_content {
    margin-bottom: 350px;
  }
}
@media screen and (max-width: 376px) {
  .heroes_title h2 {
    font-size: 14px;
  }
  .heroes_title h5 {
    font-size: 10px;
    max-width: 180px;
  }
  .heroes_description div span {
    font-size: 12px;
  }
  .heroes_description div h2 {
    font-size: 10px;
  }
}
